@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,700&display=swap);
.topbar{-webkit-backface-visibility:hidden;backface-visibility:hidden;background-color:#ec7000;box-shadow:0 4px 10px rgba(0,0,0,0.07);left:0;padding:10px 16px;position:fixed;top:0;width:100%}.topbar .container{display:flex;justify-content:center;align-items:center}.topbar__logo{display:inline-block;height:40px}.topbar__logo svg{height:100%}

.filters{align-items:center;display:flex;flex-wrap:nowrap;padding:24px 0}.filters__search{background:#fff;border-radius:5px;border:1px solid rgba(0,0,0,0.1);box-shadow:0 2px 2px rgba(0,0,0,0.07);display:flex;flex-wrap:nowrap;height:40px;justify-content:stretch;margin-right:8px;overflow:hidden;width:100%}.filters__search__input{background:transparent;border:none;color:#ec7000;font-size:1.4rem;font-weight:bold;outline:none;padding:5px 10px;width:100%}.filters__search__icon{background:transparent;font-size:1.4rem;color:#9fa9bc}.filters__search__icon:hover{color:#ec7000}::-webkit-input-placeholder{color:#9fa9bc;font-weight:normal}:-ms-input-placeholder{color:#9fa9bc;font-weight:normal}::-ms-input-placeholder{color:#9fa9bc;font-weight:normal}::placeholder{color:#9fa9bc;font-weight:normal}.filters__item{align-items:center;background-color:#fff;border-radius:5px;border:1px solid rgba(0,0,0,0.1);box-shadow:0 2px 2px rgba(0,0,0,0.07);color:#9fa9bc;display:flex;flex-wrap:nowrap;font-size:1.3rem;font-weight:normal;justify-content:center;margin:0 8px;padding:10px;white-space:nowrap}.filters__item i{color:#9fa9bc;margin-top:-5px;margin-left:8px}.filters__item.is-selected{background-color:#ec7000;border:1px solid rgba(0,0,0,0.1);color:#fff}.filters__item.is-selected i{color:#fff}

.contacts{width:100%;display:flex;flex-direction:column;padding:16px}@media screen and (min-width: 728px){.contacts{padding:16px 0}}

.popup-box{position:fixed;background:transparent;width:100%;border:1px solid rgba(0,0,0,0.1);z-index:1050;height:100vh;top:50%;left:50%;transform:translate(-50%, -50%)}.box{position:relative;width:50%;margin:0 auto;height:auto;max-height:70vh;border:1px solid rgba(0,0,0,0.1);margin-top:calc(100vh - 85vh - 20px);background:#fff;border-radius:5px;padding:20px;border:1px solid #999;overflow:auto}.popup__header{display:flex;align-items:flex-start;justify-content:space-between;padding:1rem;border-bottom:1px solid #dee2e6;border-top-left-radius:calc(.3rem - 1px);border-top-right-radius:calc(.3rem - 1px)}.popup__header>span{display:flex;align-items:flex-start;justify-content:space-between;padding:1rem;border-top-left-radius:calc(.3rem - 1px);border-top-right-radius:calc(.3rem - 1px);font-size:15px;font-weight:bold}.popup__body{display:flex;position:relative;flex:1 1 auto;padding:20px}.popup__footer{display:flex;flex-wrap:wrap;align-items:center;justify-content:flex-end;padding:.75rem;border-top:1px solid #dee2e6;border-top-left-radius:calc(.3rem - 1px);border-top-right-radius:calc(.3rem - 1px)}.btn{align-items:center;background-color:#fff;border-radius:5px;border:1px solid rgba(0,0,0,0.1);box-shadow:0 2px 2px rgba(0,0,0,0.07);color:#9fa9bc;display:flex;flex-wrap:nowrap;font-size:1.3rem;font-weight:normal;justify-content:center;margin:0 8px;padding:10px;white-space:nowrap}.btn__save{background-color:#ec7000;border:1px solid rgba(0,0,0,0.1);color:#fff}ul{list-style-type:none;margin:10px;padding:10px;align-items:center}

.contact{align-items:center;background-color:white;border-radius:5px;border:1px solid rgba(0,0,0,0.1);box-shadow:0 2px 2px rgba(0,0,0,0.07);display:flex;justify-content:space-between;flex-wrap:nowrap;font-size:1.2rem;margin-bottom:20px;overflow:hidden;padding:5px}.contact:first-of-type{margin-bottom:5px;background-color:transparent;border:none;box-shadow:none;padding:0 5px;font-weight:bold}.contact:first-of-type .contact__avatar{height:0;border:none}.contact:first-of-type .contact__data:nth-child(2){color:#000}.contact__avatar{border-radius:50px;height:35px;overflow:hidden;width:35px;border:2px solid #9fa9bc;margin-right:10px}.contact__avatar img{height:100%}.contact__data{width:calc(100% / 6);padding:0 10px}.contact__data:nth-child(2){color:#4c84ff}.contact__button{background-color:#0dee0d}.contact__item{align-items:center;background-color:#fff;border-radius:5px;border:1px solid rgba(0,0,0,0.1);box-shadow:0 2px 2px rgba(0,0,0,0.07);color:#9fa9bc;display:flex;flex-wrap:nowrap;font-size:1.3rem;font-weight:normal;justify-content:center;margin:0 8px;padding:10px;white-space:nowrap}.contact__item.button{background-color:#ec7000;border:1px solid rgba(0,0,0,0.1);color:#fff}.contact__item.button.disabled{background-color:#fff;border:1px solid rgba(0,0,0,0.1);color:#9fa9bc}

.loading{background:transparent;height:71px;margin:0 auto 10px;position:relative;width:71px}.loading__text,.loading .loading{position:relative;z-index:2}.loading__text{-webkit-animation-delay:1000ms;animation-delay:1000ms;color:#ec7000;font-family:'Raleway', sans-serif;font-size:12px;font-weight:bold;text-align:center;text-shadow:0 0 0 #ec7000}.loading span{-webkit-animation:soundBar 1700ms infinite ease-in-out;animation:soundBar 1700ms infinite ease-in-out;background:#ec7000;border-radius:2px;bottom:0;display:block;height:2px;position:absolute;width:8px}.loading span:nth-child(1){left:0}.loading span:nth-child(2){-webkit-animation-delay:100ms;animation-delay:100ms;left:9px}.loading span:nth-child(3){-webkit-animation-delay:200ms;animation-delay:200ms;left:18px}.loading span:nth-child(4){-webkit-animation-delay:300ms;animation-delay:300ms;left:27px}.loading span:nth-child(5){-webkit-animation-delay:400ms;animation-delay:400ms;left:36px}.loading span:nth-child(6){-webkit-animation-delay:500ms;animation-delay:500ms;left:45px}.loading span:nth-child(7){-webkit-animation-delay:600ms;animation-delay:600ms;left:54px}.loading span:nth-child(8){-webkit-animation-delay:700ms;animation-delay:700ms;left:63px}@-webkit-keyframes soundBar{0%{background:#ec7000;height:2px}20%{background:#ec7000;height:70px}40%{background:#ec7000;height:2px}60%{background:#ec7000;height:2px}}@keyframes soundBar{0%{background:#ec7000;height:2px}20%{background:#ec7000;height:70px}40%{background:#ec7000;height:2px}60%{background:#ec7000;height:2px}}@-webkit-keyframes text-glow{0%{color:#ec7000;text-shadow:0 0 0 #ec7000}50%{color:#ec7000;text-shadow:0 0 5px #ec7000}100%{color:#ec7000;text-shadow:0 0 0 #ec7000}}@keyframes text-glow{0%{color:#ec7000;text-shadow:0 0 0 #ec7000}50%{color:#ec7000;text-shadow:0 0 5px #ec7000}100%{color:#ec7000;text-shadow:0 0 0 #ec7000}}

*,*:after,*:before{box-sizing:border-box}html{font-size:10px}body{padding-top:60px;font-family:"Open Sans", sans-serif;background-color:#f8f9fb;color:#000}a{text-decoration:none;color:#000;font-weight:bold}button{background-color:transparent;border:none;box-shadow:none;outline:none}.container{width:100%;margin:0 auto}@media screen and (min-width: 960px){.container{width:960px}}

