.topbar {
    backface-visibility: hidden;
    background-color: #ec7000;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.07);
    left: 0;
    padding: 10px 16px;
    position: fixed;
    top: 0;
    width: 100%;
  }
  
  .topbar .container {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .topbar__logo {
    display: inline-block;
    height: 40px;
  }
  
  .topbar__logo svg {
    height: 100%;
  }
  